import React, { Component } from 'react';
import Dice from './Dice'
import Button from './Button'
import Player from './Player';

export default class Main extends Component {
    constructor(props){
        super(props)
        this.state = {
            diceIsRoll: false,
            diceName: ['one','two','three','four','five','six'],
            playerScore: 0
        }
    }
    randomNumber = () => Math.floor(Math.random() * 6) + 1
    handleRollDice = () => {
        let dice1 = this.randomNumber()
        let dice2 = this.randomNumber()
        this.setState({
            diceIsRoll: true,
            dice1: dice1,
            dice2: dice2,
        })
        this.setState((state,props) => ({
            playerScore: state.playerScore + (state.dice1 + state.dice2)
        }))
    }
    handleSaveTempScore = () => {
        console.log(this.state);
        return (<p>{this.state.playerScore}</p>)
    }
    render(){
        const {diceIsRoll,diceName,dice1,dice2,playerScore} = this.state
        return (
            <div className="main-page">
                <div className='players-wrap'>
                    <Player playerScore={playerScore} name='player 1'/>
                    <Player playerScore={playerScore} name='player 2'/>
                </div>
                { diceIsRoll &&  <div>
                <Dice randomNumStr={diceName[dice1-1]}/>
                <Dice randomNumStr={diceName[dice2-1]}/>
                
                </div>}
                {diceIsRoll && <div>{this.handleSaveTempScore()}</div> }
                <Button name='roll' click={this.handleRollDice}/>
                <Button name='hold' click={this.handleSaveScore}/>
                <Button name='go back to homepage' click={this.props.backBtn}/>
            </div>
        );
    }
}
